const axios = require('axios');
const store = require('store');
const cfg = require('../../darklaunch.config.json');
const socketIOClient = require('socket.io-client');
const mobx = require('mobx');

class Darklauncher {
    constructor() {
        if (!this.loadData(cfg)) {
            return;
        }

        // Generate token, store locally, use in GET for tracking.
        if (cfg.settings.enableStreaming) {
            const socket = socketIOClient(cfg.settings.url, {
                query: {
                    createdby: cfg.settings.token,
                    api_version: cfg.version
                }
            });

            socket.on('state', (data) => {
                this.configs = data;
                store.set('darklaunch', this.configs);
            });

            socket.on('update', (data) => {
                Object.keys(data).forEach((key) => {
                    this.configs[key] = data[key];
                });
                store.set('darklaunch', this.configs);
            });
        } else {
            axios
                .get(
                    `${cfg.settings.url}/api/darklaunch_bundle?createdby=${cfg.settings
                        .token}&api_version=${cfg.version}`
                )
                .then(({ data }) => {
                    data.map((darklaunch) => {
                        this.configs[darklaunch.code] = darklaunch.enabled;
                    });
                    const shouldRefresh = JSON.stringify(store.get('darklaunch')) !== JSON.stringify(this.configs);
                    store.set('darklaunch', this.configs);
                    if (shouldRefresh) {
                        window.location.reload();
                    }
                });
        }
    }

    loadData(cfg) {
        if (!cfg) {
            return false;
        }
        if (!cfg.settings.url) {
            cfg.settings.url = 'https://www.darklaunch.me/';
        }
        mobx.extendObservable(this, {
            configs: store.get('darklaunch') || {},
            darklaunchID: store.get('darklaunchID') || {}
        });
        return true;
    }

    isEnabled(darklaunchFlag) {
        const darklookup = store.get('darklaunch') || {};
        return darklookup[darklaunchFlag] || false;
    }

    setUser(uuid) {
        this.uuid = uuid;
        localStorage.setItem('darklaunchID', uuid);
    }
}

module.exports = new Darklauncher(); // Support Require and Import
